html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

* {
  max-height: 1000000px;
}

:root {
  --bg-color-01: #F5F5F7;
  --bg-color-02: #DADADA;
  --bg-color-03: #EEEEF0;
  --purple: #6B60FE;
  --text-color: #2D2D2D;
  --card-shadow: rgba(107, 96, 254, 0.7);
}

.dark-theme {
  --bg-color-01: #13182A;
  --bg-color-02: #1C1C21;
  --bg-color-03: #00030F;
  --purple: #9487DF;
  --text-color: #fff;
  --card-shadow: rgba(148, 135, 223, 0.7);
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

.gm-style img {
  max-width: none;
}

body {
  background: var(--bg-color-01);
  color: var(--text-color);
  font: 400 16px/1.5 "Montserrat", "Arial", "Helvetica", sans-serif;
  min-width: 320px;
}

p, ul, ol, dl {
  margin: 0 0 1em;
}

a {
  text-decoration: underline;
  color: inherit;
}
a:hover, a:focus {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
  font-weight: 600;
  margin: 0 0 0.5em;
  color: inherit;
}

h1 {
  font-size: 48px;
}

h2 {
  font-size: 42px;
}

h3 {
  font-size: 38px;
}

h4 {
  font-size: 30px;
}

h5 {
  font-size: 24px;
}

h6 {
  font-size: 20px;
}

form,
fieldset {
  margin: 0;
  padding: 0;
  border-style: none;
}

input[type=text],
input[type=tel],
input[type=email],
input[type=search],
input[type=password],
textarea {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  box-sizing: border-box;
  border: 1px solid #999;
  padding: 10px;
}
input[type=text]:focus,
input[type=tel]:focus,
input[type=email]:focus,
input[type=search]:focus,
input[type=password]:focus,
textarea:focus {
  border-color: #000;
}
input[type=text]::-webkit-input-placeholder,
input[type=tel]::-webkit-input-placeholder,
input[type=email]::-webkit-input-placeholder,
input[type=search]::-webkit-input-placeholder,
input[type=password]::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: inherit;
}
input[type=text]::-moz-placeholder,
input[type=tel]::-moz-placeholder,
input[type=email]::-moz-placeholder,
input[type=search]::-moz-placeholder,
input[type=password]::-moz-placeholder,
textarea::-moz-placeholder {
  opacity: 1;
  color: inherit;
}
input[type=text]:-moz-placeholder,
input[type=tel]:-moz-placeholder,
input[type=email]:-moz-placeholder,
input[type=search]:-moz-placeholder,
input[type=password]:-moz-placeholder,
textarea:-moz-placeholder {
  color: inherit;
}
input[type=text]:-ms-input-placeholder,
input[type=tel]:-ms-input-placeholder,
input[type=email]:-ms-input-placeholder,
input[type=search]:-ms-input-placeholder,
input[type=password]:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: inherit;
}
input[type=text].placeholder,
input[type=tel].placeholder,
input[type=email].placeholder,
input[type=search].placeholder,
input[type=password].placeholder,
textarea.placeholder {
  color: inherit;
}

select {
  -webkit-border-radius: 0;
}

textarea {
  resize: vertical;
  vertical-align: top;
}

button,
input[type=button],
input[type=reset],
input[type=file],
input[type=submit] {
  -webkit-appearance: none;
  -webkit-border-radius: 0;
  cursor: pointer;
}

@font-face {
  font-family: "icomoon";
  src: url("../fonts/icomoon.eot?ky7w52");
  src: url("../fonts/icomoon.eot?ky7w52#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf?ky7w52") format("truetype"), url("../fonts/icomoon.woff?ky7w52") format("woff"), url("../fonts/icomoon.svg?ky7w52#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
[class^=ico-], [class*=" ico-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ico-day:before {
  content: "\e908";
}

.ico-night:before {
  content: "\e900";
}

.ico-arrow:before {
  content: "\e901";
}

.ico-email:before {
  content: "\e902";
}

.ico-github:before {
  content: "\e903";
}

.ico-instagram:before {
  content: "\e904";
}

.ico-linkedin:before {
  content: "\e905";
}

.ico-phone:before {
  content: "\e906";
}

.ico-telegram:before {
  content: "\e907";
}

h1, h2, h3, h4, h5, h6 {
  color: var(--purple);
}

mark {
  background: none;
  color: var(--purple);
  font-weight: 500;
}

.container {
  padding: 0 20px;
  width: 100%;
  margin: 0 auto;
}

.btn {
  transition: color 0.2s ease-in-out;
  background: transparent;
  padding: 20px;
  font-size: 20px;
  line-height: 1;
  color: var(--purple);
  font-weight: 500;
  letter-spacing: -0.1px;
  display: inline-block;
  min-width: 100px;
  text-align: center;
  text-decoration: none;
  border: 1px solid var(--purple);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}
.btn span {
  position: relative;
  z-index: 1;
}
.btn:before {
  width: 200%;
  height: 200%;
  content: "";
  background: var(--purple);
  position: absolute;
  top: var(--y, 50%);
  left: var(--x, 50%);
  transform: translate(-50%, -50%) scale(0);
  transition: transform 0.3s ease-out;
  border-radius: 100%;
  z-index: 0;
}
.btn:hover {
  color: var(--bg-color-03);
}
.btn:hover:before {
  transform: translate(-50%, -50%) scale(2);
}

.slick-arrow {
  width: 32px;
  height: 32px;
  transition: background 0.2s ease-in-out, border-color 0.2s ease-in-out;
  background: none;
  border: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex !important;
  align-items: center;
  justify-content: center;
  text-indent: -9999px;
  z-index: 1;
  border: 2px solid transparent;
  border-radius: 100%;
}
.slick-arrow:before {
  width: 20px;
  height: 2px;
  transition: width 0.2s ease-in-out, color 0.2s ease-in-out;
  content: "";
  background: var(--purple);
  display: block;
  position: relative;
}
.slick-arrow:after {
  width: 12px;
  height: 12px;
  transition: right 0.2s ease-in-out, left 0.2s ease-in-out, margin 0.2s ease-in-out, color 0.2s ease-in-out;
  content: "";
  display: block;
  position: relative;
  border-right: 2px solid var(--purple);
  border-bottom: 2px solid var(--purple);
  transform: rotate(-45deg);
}
.slick-arrow.slick-prev {
  flex-direction: row-reverse;
  left: -44px;
}
.slick-arrow.slick-prev:after {
  transform: rotate(135deg);
  margin-right: -12px;
}
.slick-arrow.slick-next {
  right: -44px;
}
.slick-arrow.slick-next:after {
  margin-left: -12px;
}
.slick-arrow:hover {
  border-color: var(--purple);
}
.slick-arrow:hover:before {
  background: var(--purple);
  width: 0;
}
.slick-arrow:hover:after {
  border-color: var(--purple);
  margin: 0;
}
.slick-arrow:hover.slick-prev:after {
  right: -2px;
  margin-right: 0;
}
.slick-arrow:hover.slick-next:after {
  left: -2px;
  margin-left: 0;
}

.title-block {
  text-align: center;
}
.title-block h1, .title-block h2, .title-block h3, .title-block h4, .title-block h5, .title-block h6 {
  text-transform: uppercase;
}

.project-card {
  transition: box-shadow 0.2s ease-in-out;
  background: var(--bg-color-02);
  padding: 15px;
  color: var(--text-color);
  border-radius: 20px;
  font-size: 12px;
  line-height: 1.33;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  text-decoration: none;
  box-shadow: 0 4px 4px rgba(148, 135, 223, 0);
}
.project-card:hover {
  box-shadow: 0 0 30px var(--card-shadow);
}
.project-card:hover .ico-github {
  color: var(--purple);
}
.project-card h6 {
  font-size: 18px;
  line-height: 1.22;
  font-weight: 700;
}
.project-card .img-holder {
  transition: transform 0.2s ease-in-out;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 20px;
}
.project-card .img-holder img {
  height: 150px;
  width: 100%;
  max-width: none;
  object-fit: cover;
  object-position: top;
}
.project-card .describe-block {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.project-card .top-block {
  display: flex;
  flex-grow: 1;
  margin-bottom: 3px;
}
.project-card .bottom-block {
  display: flex;
  align-items: center;
}
.project-card .bottom-block .tags {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-right: 10px;
  max-width: 100%;
}
.project-card .ico-github {
  transition: color 0.2s ease-in-out;
  margin-left: auto;
}
@media (min-width: 480px){
  h1 {
    font-size: 60px;
  }
  h2 {
    font-size: 46px;
  }
  h3 {
    font-size: 42px;
  }
  h4 {
    font-size: 34px;
  }
  h5 {
    font-size: 26px;
  }
  h6 {
    font-size: 24px;
  }
}
@media (min-width: 768px){
  .container {
    padding: 0 30px;
  }
  .project-card {
    padding: 20px;
  }
  .project-card:hover .img-holder {
    transform: scale(1.05);
  }
  .project-card .img-holder img {
    height: 200px;
  }
}
@media (min-width: 1024px){
  h1 {
    font-size: 96px;
  }
  h2 {
    font-size: 60px;
  }
  h3 {
    font-size: 54px;
  }
  h4 {
    font-size: 40px;
  }
  h5 {
    font-size: 32px;
  }
  h6 {
    font-size: 28px;
  }
  .container {
    padding: 0 40px;
    max-width: 1280px;
  }
  .slick-arrow {
    width: 50px;
    height: 50px;
  }
  .slick-arrow:before {
    width: 32px;
    height: 2px;
  }
  .slick-arrow:after {
    width: 20px;
    height: 20px;
    border-right: 2px solid var(--purple);
    border-bottom: 2px solid var(--purple);
  }
  .slick-arrow.slick-prev {
    left: -60px;
  }
  .slick-arrow.slick-prev:after {
    margin-right: -21px;
  }
  .slick-arrow.slick-next {
    right: -60px;
  }
  .slick-arrow.slick-next:after {
    margin-left: -21px;
  }
  .slick-arrow:hover.slick-prev:after {
    right: -5px;
  }
  .slick-arrow:hover.slick-next:after {
    left: -5px;
  }
}
@media (min-width: 1200px){
  .slick-arrow.slick-prev {
    left: -90px;
  }
  .slick-arrow.slick-next {
    right: -90px;
  }
}